import React, { Component } from 'react';
import { Form, Row, Col, Input } from 'antd';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import { BtnSearch, BtnClear } from 'components/common/button';
import { requiredRule } from 'utils/validateRules';
import { SelectAjax } from 'components/common';
import { getYearEducations } from 'services/yearEducationService';
import { getResgisterSessionSelect } from 'services/registerSession';

import './style.scss';

@Form.create({ name: 'formTeacher' })
class FormFilterReward extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams(values);
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
    updateQueryParams();
  };

  render() {
    const { getFieldDecorator, getFieldValue, setFieldsValue } = this.props.form;
    const yearEduId = getFieldValue('yearEduId');
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={5}>
            <Form.Item label="Mã sinh viên">
              {getFieldDecorator('studentCode', {
                rules: [requiredRule],
              })(
                <Input />
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="Năm học">
              {getFieldDecorator('yearEduId', {
                rules: [requiredRule],
              })(
                <SelectAjax
                  service={getYearEducations}
                  setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="Đợt đăng ký">
              {getFieldDecorator('registerSessionId', {
                rules: [requiredRule],
              })(
                <SelectAjax
                  service={getResgisterSessionSelect}
                  setFieldsValue={setFieldsValue}
                  allowGetData={!!yearEduId}
                  params={{ yearEduId, fullFormat: true, }}
                  keyLabel="label"
                  keyValue="value"
                />
              )}
            </Form.Item>
          </Col>
          <Col>
            <div className="btn-group" style={{ float: 'right' }}>
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormFilterReward;



