import React, { Component, Fragment } from 'react';
import { Row, Form, Col, Input, Divider } from 'antd';
import { Btn } from 'components/common/button';
import { TableForm } from 'components/common';

@Form.create({ name: 'formTableQualifiedDoThesis' })
class TableQualifiedDoThesis extends Component {
  constructor(props) {
    super(props);
    this.refTable = React.createRef();
    this.state = {};
  }

  getTableData = () => {
    return this.refTable.current.getTableData();
  }

  createColumns = () => {
    const columns1 = [
      {
        title: 'Mã sinh viên',
        dataIndex: 'studentCode',
        key: 'studentCode',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'classname',
        key: 'classname',
      },
      {
        title: 'Môn',
        dataIndex: 'subject',
        key: 'subject',
      },
      {
        title: 'Đợt',
        dataIndex: 'registerSession',
        key: 'registerSession',
      },
      {
        title: 'Học kỳ',
        dataIndex: 'semester',
        key: 'semester',
      },
      {
        title: 'Năm',
        dataIndex: 'yearEdu',
        key: 'yearEdu',
      },
    ];
    return columns1;
  };

  render() {
    const {
      reload,
      rowKey,
      data,
      info,
      handleSelectedRows,
      form,
      cancelDisabledSubject,
      disableNoSelectedRowBtn,
    } = this.props;
    const columns = this.createColumns();
    const { getFieldDecorator } = this.props.form;
    return (
      <Fragment>
        <Divider>Thông tin sinh viên</Divider>
        <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
          <Row gutter={6}>
            <Col xs={24} sm={12} md={6}>
              <Form.Item label="Họ tên">
                {getFieldDecorator('fullNameLabel', {
                  initialValue: info.fullname,
                })(
                  <Input disabled />
                )}
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Form.Item label="Ngày sinh">
                {getFieldDecorator('dobLabel', {
                  initialValue: info.dob,
                })(
                  <Input disabled />
                )}
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Form.Item label="Lớp">
                {getFieldDecorator('classNameLabel', {
                  initialValue: info.className,
                })(
                  <Input disabled />
                )}
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={6}>
            <Col xs={24} sm={12} md={6}>
              <Form.Item label="Hệ">
                {getFieldDecorator('trainTypeLabel', {
                  initialValue: info.trainType,
                })(
                  <Input disabled />
                )}
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Form.Item label="Khoa">
                {getFieldDecorator('facultyLabel', {
                  initialValue: info.faculty,
                })(
                  <Input disabled />
                )}
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Form.Item label="Khóa">
                {getFieldDecorator('courseLabel', {
                  initialValue: info.course,
                })(
                  <Input disabled />
                )}
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Form.Item label="CN">
                {getFieldDecorator('studyFieldLabel', {
                  initialValue: info.studyField,
                })(
                  <Input disabled />
                )}
              </Form.Item>
            </Col>
          </Row>
          <Divider>Thông tin môn học</Divider>
          <Row>
            <div className="btn-group" style={{ float: 'right' }}>
              <Btn icon="reload" className="btnSave" title="Phục hồi" onClick={cancelDisabledSubject} disabled={disableNoSelectedRowBtn} />
            </div>
          </Row>
          <TableForm
            reload={reload}
            data={data || []}
            handleSelectedRows={handleSelectedRows}
            rowKey={rowKey}
            columns={columns}
            form={form}
            ref={this.refTable}
          />
        </Form>
      </Fragment>
    );
  }
}

export default TableQualifiedDoThesis;



