import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import {
  Card,
  Row,
  Col,
  Spin,
  message
} from 'antd';
import { getQueryParams } from 'utils/urlUtil';
import { makeid } from 'utils/functionUtil';
import {
  getListDataValid
} from 'utils/formUtil';
import {
  getStudentEnrollmentHistory,
  restoreEnrollment,
} from 'services/classCreditEnrollment';
import { getStudentByCode } from 'services/student';

import StudentCheckGraduateTable from './StudentCheckGraduateTable';
import FormSearch from './FormSearch';
import './style.scss';

class ListStudentPreparingToGraduationPage extends Component {
  constructor(props) {
    super(props);
    this.refFormForProject = React.createRef();
    this.refFormRawForProject = React.createRef();
    this.state = {
      loading: false,
      paramSearch: {},
      reloadForDataIssueCer: 0,
      selectedRows: [],
      rowKey: "autoKey",
      dataStudentForIssueCer: [],
      infoStudent: {},
    };
  }

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    console.log(this.state.paramSearch);
    console.log(this.state.reloadForDataIssueCer);
    console.log(this.state.reloadForGraduateTest);
    console.log(this.state.selectedRows);
    const paramSearch = getQueryParams();
    this.setState({
      paramSearch,
    },
      () => this.handleSearch(paramSearch),
    );
  };

  handleSearch = async (
    paramSearch
  ) => {
    const { loading } = this.state;
    if (loading) {
      return;
    }
    if (
      !!paramSearch.studentCode &&
      !!paramSearch.yearEduId &&
      !!paramSearch.registerSessionId
    ) {
      this.setState({
        loading: true,
      });
      this.setState({
        loading: true,
        dataStudentForIssueCer: [],
        infoStudent: {},
      },
        async () => {
          await Promise.all([
            getStudentEnrollmentHistory(paramSearch),
            getStudentByCode(paramSearch.studentCode)
          ])
            .then((responses) => {
              console.log('responses[1]', responses[1]);
              console.log('this.state.infoStudent', this.state.infoStudent);
              const tempDataStudentForIssueCer = responses[0].data.items.map(item => {
                item.autoKey = `${makeid(8)}_${item.studentCode}`;
                return item;
              })
              this.setState({
                loading: false,
                dataStudentForIssueCer: [...tempDataStudentForIssueCer],
                infoStudent: { ...responses[1].data },
              });
            })
            .catch((error) => {
              console.log(error);
              this.setState({
                loading: false,
              });
            });
        },
      );
    }
  }

  handleSelectedRows = (selectedRows) => {
    this.setState({
      selectedRows,
    });
  }

  cancelDisabledSubject = () => {
    try {
      const { selectedRows, rowKey, paramSearch } = this.state;
      if (selectedRows && selectedRows.length) {
        const listFieldName = [];
        this.setState({
          loading: true,
        });
        getListDataValid(rowKey, listFieldName, this.refFormForProject, this.refFormRawForProject, selectedRows)
          .then(
            async (listSelectedItem) => {
              const cceIds = listSelectedItem.map(item => item.cceId);
              const formData = {
                cceIds
              };

              try {
                await restoreEnrollment(formData);
                message.success('Phục hồi môn đã hủy thành công.');
                this.setState({
                  selectedRows: [],
                  loading: false,
                },
                  () => this.handleSearch(paramSearch),
                );
              } catch (error) {
                console.log(error);
              } finally {
                this.setState({
                  loading: false,
                });
              }
            }
          )
          .catch(
            () => {
              this.setState({
                loading: false,
              });
            }
          )
      }
    } catch (error) {
      console.log(error);
    }
  }

  render() {
    // const {
    // dataStudentForIssueCer,
    // queryParams
    // } = this.props;
    const {
      dataStudentForIssueCer,
      infoStudent,
      reloadForDataIssueCer,
      rowKey,
      loading,
      selectedRows,
    } = this.state;
    const disableNoSelectedRowBtn = !(selectedRows && selectedRows.length);
    return (
      <Spin spinning={loading}>
        <Card title="Xét điều kiện khóa luận, thi tốt nghiệp">
          <Row>
            <FormSearch />
          </Row>
          {infoStudent && infoStudent.code &&
            <Row>
              <Col span={24}>
                <StudentCheckGraduateTable
                  reload={reloadForDataIssueCer}
                  data={dataStudentForIssueCer}
                  info={infoStudent}
                  ref={this.refFormForProject}
                  handleSelectedRows={this.handleSelectedRows}
                  rowKey={rowKey}
                  wrappedComponentRef={(form) => {
                    this.refFormRawForProject = form;
                  }}
                  cancelDisabledSubject={this.cancelDisabledSubject}
                  disableNoSelectedRowBtn={disableNoSelectedRowBtn}
                />
              </Col>
            </Row>
          }
        </Card>
      </Spin>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListStudentPreparingToGraduationPage />
  </Page>
);



